import React, { useState } from "react";
import SwiperCore, { Navigation, A11y } from "swiper";
import { Swiper, SwiperSlide } from "swiper/react";
import Img from "gatsby-image/withIEPolyfill";
import "react-photoswipe/lib/photoswipe.css";
import { PhotoSwipe } from "react-photoswipe";

SwiperCore.use([Navigation, A11y]);

export default function Gallery({ images }) {
  const [photoSwipe, setPhotoSwipe] = useState({ isOpen: false, index: 0 });

  let items = images.map(image => ({
    title: image.alt,
    src: image.file.thumbnail.fluid.originalImg,
    w: image.file.thumbnail.fluid.presentationWidth,
    h: image.file.thumbnail.fluid.presentationHeight,
  }));

  let options = {
    history: false,
    fullscreenEl: false,
    zoomEl: false,
    shareEl: false,
    index: photoSwipe.index,
  };

  return (
    <>
      <PhotoSwipe
        isOpen={photoSwipe.isOpen}
        items={items}
        options={options}
        onClose={() => setPhotoSwipe({ isOpen: false, index: 0 })}
      />
      <Swiper
        spaceBetween={15}
        slidesPerView={1}
        height={200}
        breakpoints={{
          568: {
            slidesPerView: 2,
          },
          1024: {
            slidesPerView: 3,
          },
        }}
        navigation
      >
        {images.map((image, index) => (
          <SwiperSlide
            key={index}
            onClick={() => setPhotoSwipe({ isOpen: true, index })}
          >
            <Img
              objectFit="cover"
              className="post-image"
              fluid={image.file.thumbnail.fluid}
              alt={image.alt}
            />
          </SwiperSlide>
        ))}
      </Swiper>
    </>
  );
}
