import React from "react";
import { graphql } from "gatsby";
import { Layout } from "../components/layout";
import Gallery from "../components/gallery";

export default function Template({ data }) {
  const { markdownRemark } = data;
  const { frontmatter, html } = markdownRemark;

  return (
    <Layout title={frontmatter.title} datePublished={frontmatter.isoDate}>
      <article className="post">
        <div className="post-header">
          <h1 className="post-title">{frontmatter.title}</h1>
          <p className="post-date">{frontmatter.date}</p>
        </div>
        {frontmatter.tags && (
          <div className="tags">
            {frontmatter.tags.map(tag => {
              return (
                <a key={tag} href="#" className="tag">
                  {tag}
                </a>
              );
            })}
          </div>
        )}
        {frontmatter.images && <Gallery images={frontmatter.images} />}
        <div
          className="post-content"
          dangerouslySetInnerHTML={{ __html: html }}
        />
      </article>
    </Layout>
  );
}
export const pageQuery = graphql`
  query($path: String!) {
    markdownRemark(frontmatter: { path: { eq: $path } }) {
      html
      frontmatter {
        isoDate: date(formatString: "YYYY-MM-DDTHH:mm:ss.sssZ")
        date(formatString: "DD.MM.YYYY")
        path
        title
        tags
        images {
          alt
          file {
            thumbnail: childImageSharp {
              fluid(maxWidth: 600, cropFocus: CENTER) {
                ...GatsbyImageSharpFluid_withWebp
                originalImg
                presentationHeight
                presentationWidth
              }
            }
          }
        }
      }
    }
  }
`;
